import React, { Component } from "react";
import "./Checkbox.css";

class Checkbox extends Component {
  render() {
    const isChecked = this.props.personID === this.props.id;

    return (
      <label className={`radio-box box-checked-${isChecked}`}>
        <input
          type="radio"
          name="personID"
          value={this.props.id}
          checked={this.props.personID === this.props.id}
          onChange={this.props.handleEvent}
        />
        {this.props.name}
      </label>
    );
  }
}

export default Checkbox;
