import React, { Component } from "react";
import "./Main.css";
import Checkbox from "./Checkbox/Checkbox.js";
import arrowLeft from "./left.png";
import arrowRight from "./right.png";

import data from "./data";

class Main extends Component {
  constructor() {
    super();
    this.handleEvent = this.handleEvent.bind(this);
    this.state = { persons: data, personID: "0", pointer: 0 };
  }

  handleEvent(event) {
    const { value, name } = event.target;

    switch (name) {
      case "leftArrow":
        this.setState(prev => ({
          pointer: prev.pointer - 1 >= 0 ? prev.pointer - 1 : prev.pointer
        }));
        break;

      case "rightArrow":
        const quotesAmount = this.state.persons[this.state.personID].quotes
          .length;
        this.setState(prev => ({
          pointer:
            prev.pointer + 1 < quotesAmount ? prev.pointer + 1 : prev.pointer
        }));
        break;

      default:
        this.setState({ personID: value, pointer: 0 });
        break;
    }
  }

  render() {
    //Makes array of Checkbox components with pushed props
    const checkboxPersons = this.state.persons.map(person => (
      <Checkbox
        key={person.id}
        id={person.id}
        name={person.name}
        personID={this.state.personID}
        handleEvent={this.handleEvent}
      />
    ));

    //Accessing quote and person
    const quoteActual = this.state.persons[this.state.personID].quotes[
      this.state.pointer
    ];
    const personActual = this.state.persons[this.state.personID].name;

    return (
      <main className="main" id="main">
        <div className="container">
          <div className="radio-boxes" id="radio-boxes">
            {checkboxPersons}
          </div>
          <div className="quote-area">
            <div className="quote-window">
              <p className="quote-window__quote">{`"${quoteActual}"`}</p>
              <p className="quote-window__person">{`(¬©) ${personActual}`}</p>
            </div>
            <div className="quote-scroller">
              <img
                src={arrowLeft}
                alt="Prev"
                name="leftArrow"
                onClick={this.handleEvent}
              />
              <img
                src={arrowRight}
                alt="Next"
                name="rightArrow"
                onClick={this.handleEvent}
              />
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default Main;
